body {
	margin: 0;
	display: flex;
	height: 100vh;
	justify-content: center;
	align-items: center;
}

.Clock {
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	width: 400px;
	height: 400px;
	margin: auto;
	border-radius: 100%;
	background: #eee;
	border: 20px solid #999;
	box-shadow: 3px 3px 7px rgba(0,0,0,0.5), inset 3px 3px 7px rgba(0,0,0,0.5);
	font-family: Oswald;
	font-size: 20px;
}

.hand {
	position: absolute;
	background: #000;
	transform-origin: bottom;
	box-shadow: 0px 0px 5px rgba(0,0,0,0.5);
}

.dot {
	border-radius: 100%;
	width: 12px;
	height: 12px;
	background: #ff0000;
	box-shadow: 0px 0px 5px rgba(0,0,0,0.5);
	z-index: 3;
}

.second {
	top: calc(50% - 150px);
	left: calc(50% - 1px);
	width: 2px;
	height: 150px;
	background: #ff0000;
	z-index: 2;
}

.minute {
	top: calc(50% - 160px);
	left: calc(50% - 2px);
	width: 4px;
	height: 160px;
}

.hour {
	top: calc(50% - 100px);
	left: calc(50% - 3px);
	width: 6px;
	height: 100px;	
}

.digit {
	position: absolute;
}

.digit12 {
	top: 10px;
}

.digit3 {
	right: 10px;
}

.digit6 {
	bottom: 10px;
}

.digit9 {
	left: 10px;
}